.slider-layout {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slider-layout .slider-content {
  width: 100%;
  height: 2px;
  position: relative;
  transition: height 0.2s;
  cursor: pointer;
}
.slider-layout .slider-max-line {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  border-radius: 4px;
}
.slider-layout .slider-visibel-line {
  position: absolute;
  background: #5B7EAC;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 3;
  cursor: pointer;
  border-radius: 4px;
}
.slider-layout .slider-current-line {
  position: absolute;
  background-color: #ffaa00;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 3;
  border-radius: 4px;
}
.slider-layout .drag-change-icon {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin-left: -3px;
  border: 1px solid #fff;
  background-color: #ffaa00;
  top: -3px;
  z-index: 4;
}
.slider-layout .slide-tips {
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  left: 0;
  top: -34px;
  transform: translateX(-50%);
  font-size: 12px;
  color: #fff;
  border-radius: 4px;
  z-index: 3;
  padding: 2px 4px;
}
.slider-layout:hover .slider-content {
  height: 4px;
}
.slider-layout:hover .slider-content .drag-change-icon {
  width: 10px;
  height: 10px;
  top: -4px;
  margin-left: -5px;
}
